import echarts from 'echarts';

var moment = require('moment'); // require("echarts/theme/macarons"); // echarts theme


import { debounce } from '@/utils';
export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Array,
      required: true
    }
  },
  data: function data() {
    return {
      chart: null,
      ranges: ['0-30', '61-100', '101-150', '151-200', '200以上'],
      sidebarElm: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler: function handler(val) {
        this.setOptions(val);
        this.initChart(this.chartData);
      }
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.initChart();

    if (this.autoResize) {
      this.__resizeHandler = debounce(function () {
        if (_this.chart) {
          _this.chart.resize();
        }
      }, 100);
      window.addEventListener('resize', this.__resizeHandler);
    } // 监听侧边栏的变化


    this.sidebarElm = document.getElementsByClassName('sidebar-container')[0];
    this.sidebarElm && this.sidebarElm.addEventListener('transitionend', this.sidebarResizeHandler);
  },
  beforeDestroy: function beforeDestroy() {
    if (!this.chart) {
      return;
    }

    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler);
    }

    this.sidebarElm && this.sidebarElm.removeEventListener('transitionend', this.sidebarResizeHandler);
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    sidebarResizeHandler: function sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler();
      }
    },
    setOptions: function setOptions(data) {
      var self = this;
      var series = [{
        name: '',
        itemStyle: {
          normal: {
            lineStyle: {
              color: '#4289f2',
              width: 2
            }
          }
        },
        smooth: true,
        type: 'bar',
        data: data,
        animationDuration: 2800,
        animationEasing: 'cubicInOut'
      }];
      this.chart.setOption({
        color: ['#4289f2'],
        title: {
          text: '',
          textStyle: {
            fontSize: 16,
            color: '#404040',
            // lineHeight: 30,
            fontWeight: 400
          }
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 60,
          containLabel: true
        },
        xAxis: {
          data: this.ranges,
          boundaryGap: true,
          axisTick: {
            show: false
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          }
        },
        series: series
      });
    },
    initChart: function initChart() {
      this.chart = echarts.init(this.$el);
      this.chart.clear();
      this.setOptions(this.chartData);
    }
  }
};