import "core-js/modules/es7.object.values";
import "core-js/modules/web.dom.iterable";

//
//
//
//
var moment = require('moment');

import echarts from 'echarts';
import { config } from "../../config.js"; // require("echarts/theme/macarons"); // echarts theme

import { debounce } from '@/utils';
export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Object,
      required: true
    },
    dateRange: {
      type: Array,
      required: true
    }
  },
  data: function data() {
    return {
      chart: null,
      hours: ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'],
      // dates: [1, 2, 3, 4, 5, 6, 7],
      sidebarElm: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler: function handler(val) {
        this.setOptions(val);
        this.initChart(this.chartData);
      }
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.initChart();

    if (this.autoResize) {
      this.__resizeHandler = debounce(function () {
        if (_this.chart) {
          _this.chart.resize();
        }
      }, 100);
      window.addEventListener('resize', this.__resizeHandler);
    } // 监听侧边栏的变化


    this.sidebarElm = document.getElementsByClassName('sidebar-container')[0];
    this.sidebarElm && this.sidebarElm.addEventListener('transitionend', this.sidebarResizeHandler);
  },
  beforeDestroy: function beforeDestroy() {
    if (!this.chart) {
      return;
    }

    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler);
    }

    this.sidebarElm && this.sidebarElm.removeEventListener('transitionend', this.sidebarResizeHandler);
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    sidebarResizeHandler: function sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler();
      }
    },
    setOptions: function setOptions(datas, dateRange) {
      var xlabel;
      var selected = {};
      Object.values(config.names).forEach(function (item) {
        selected[item] = false;
      });
      selected['支付金额'] = true;
      var start = moment(this.dateRange[0]);
      var end = moment(this.dateRange[1]);

      if (moment(start).isSame(end)) {
        xlabel = this.hours;
      } else if (!moment(start).isSame(end)) {
        var diff = end.diff(start, 'days');
        var dates = [];
        var tag = 0;
        dates.push(start.format('MM-DD'));

        while (tag < diff) {
          dates.push(start.add(1, 'days').format('MM-DD'));
          tag++;
        }

        xlabel = dates;
      }

      var series = [];

      for (var key in datas) {
        series.push({
          name: config.names[key],
          itemStyle: {
            normal: {
              lineStyle: {
                width: 2
              }
            }
          },
          smooth: true,
          type: 'line',
          data: datas[key],
          animationDuration: 2800,
          animationEasing: 'cubicInOut'
        });
      }

      console.log(series, '00000');
      this.chart.setOption({
        // color: ['#f4e0c6', '#efb365', '#EB8705', '#d48265', '#91c7ae'],
        legend: {
          right: 50,
          selected: selected
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 60,
          containLabel: true
        },
        xAxis: {
          data: xlabel,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          }
        },
        series: series
      });
    },
    initChart: function initChart() {
      this.chart = echarts.init(this.$el);
      this.chart.clear();
      this.setOptions(this.chartData, this.dateRange);
    }
  }
};